.slider{
    position: relative;
}

.sliderArrow{
    width: 80px;
    height: 100%;
    background-clip: content-box;
    padding: 20px 0;
    transition: 400ms all ease-in-out;
    cursor: pointer;
    box-sizing: border-box;
    z-index: 10;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: hidden;
}

.left{
    top: 0;
    left: 0;
}

.left:hover{
    background: rgba(20,20,20, 0.5);
    transition: 400ms all ease-in-out;
}

.right{
    top: 0;
    right: 0;
}

.right:hover{
    background: rgba(20,20,20, 0.5);
    transition: 400ms all ease-in-out;
}

.arrow{
    transition: 400ms all ease-in-out;
}

.arrow:hover{
    transition: 400ms all ease-in-out;
    transform: scale(1.5);
}

.slider:hover .sliderArrow{
    transition: 400ms all ease-in-out;
    visibility: visible;
}

.rowPosters{
    display: flex;
    overflow-y: hidden;
    overflow-y: scroll;
    padding: 20px 0 20px 20px;
    scroll-behavior: smooth;
}

.rowPosters::-webkit-scrollbar {
    display: none;
}

.rowPoster {
    object-fit: contain;
    width: 100%;
    max-height: 144px;
    margin-right: 10px;
    transform: transform 450ms;
    border-radius: 4px;
}

.rowPoster:hover {
    transform: scale(1.08);
}

@media screen and (min-width: 1200px) {
    .rowPoster {
        max-height: 160px;
    }
}

@media screen and (max-width: 768px) {
    .rowPoster {
        max-height: 280px;
    }
}